import { getAddress, isAddress } from '@ethersproject/address';
import { BigNumber, isBigNumberish } from '@ethersproject/bignumber/lib/bignumber';
import { isHexString } from '@ethersproject/bytes';
import { Contract } from '@ethersproject/contracts';
import { JsonRpcProvider } from '@ethersproject/providers';
import { keccak256 as solidityKeccak256 } from '@ethersproject/solidity';
import { multicall, call, sendTransaction } from '../../utils';
import getProvider from '../../utils/provider';
import { _TypedDataEncoder } from "@ethersproject/hash";

const EIP712_TYPES = {
  "Transaction": [
    {
      "name": "to",
      "type": "address"
    },
    {
      "name": "value",
      "type": "uint256"
    },
    {
      "name": "data",
      "type": "bytes"
    },
    {
      "name": "operation",
      "type": "uint8"
    },
    {
      "name": "nonce",
      "type": "uint256"
    }
  ]
}

const ModuleAbi = [
  // Events
  "event ProposalQuestionCreated(bytes32 indexed questionId, string indexed proposalId)",

  // Read functions
  "function executor() view returns (address)",
  "function oracle() view returns (address)",
  "function questionCooldown() view returns (uint32)",
  "function buildQuestion(string proposalId, bytes32[] txHashes) view returns (string)",
  "function executedPropsalTransactions(bytes32 questionHash, bytes32 txHash) view returns (bool)",

  // Write functions
  "function addProposal(string proposalId, bytes32[] txHashes)",
  "function executeProposal(bytes32 questionId, string proposalId, bytes32[] txHashes, address to, uint256 value, bytes data, uint8 operation, uint256 nonce)",
];

const OracleAbi = [
  // Read functions
  "function resultFor(bytes32 question_id) view returns (bytes32)",
  "function getFinalizeTS(bytes32 question_id) view returns (uint32)",
];


export interface ModuleTransaction {
  to: string,
  value: string,
  data: string,
  operation: string,
  nonce: string
}


export interface ProposalDetails {
  dao: string,
  oracle: string,
  cooldown: number,
  proposalId: string,
  questionId: string | undefined,
  executionApproved: boolean,
  finalizedAt: number | undefined,
  nextTxIndex: number | undefined,
  transactions: ModuleTransaction[],
  txHashes: string[]
}

const buildQuestion = async (proposalId: string, txHashes: string[]) => {
  const hashesHash = solidityKeccak256(["bytes32[]"], [txHashes]).slice(2);
  return `${proposalId}␟${hashesHash}`
}

const getNextTxIndex = async (provider: JsonRpcProvider, moduleAddress: string, proposalId: string, txHashes: string[]) => {
  // Call syntax is [to, [params]]
  const question = await buildQuestion(proposalId, txHashes)
  const questionHash = solidityKeccak256(["string"], [question])
  const executionStati = (await multicall(provider.network.chainId.toString(), provider, ModuleAbi,
    txHashes.map((txHash) => [moduleAddress, 'executedPropsalTransactions', [questionHash, txHash]])
  )).map((res) => res[0]);
  const nextIndexToExecute = executionStati.indexOf(false)
  return nextIndexToExecute < 0 || nextIndexToExecute >= txHashes.length ? undefined : nextIndexToExecute
};

const getModuleDetails = async (provider: JsonRpcProvider, moduleAddress: string): Promise<{ dao: string, oracle: string, cooldown: number }> => {
  const moduleDetails = await multicall(provider.network.chainId.toString(), provider, ModuleAbi,
    [[moduleAddress, 'executor'], [moduleAddress, 'oracle'], [moduleAddress, 'questionCooldown']]
  );
  return {
    dao: moduleDetails[0][0],
    oracle: moduleDetails[1][0],
    cooldown:  moduleDetails[2][0],
  }
}

const checkPossibleExecution = async (provider: JsonRpcProvider, oracleAddress: string, questionId: string | undefined): Promise<{ executionApproved: boolean, finalizedAt: number | undefined }> => {
  if (questionId) {
    try {
      const result = await multicall(provider.network.chainId.toString(), provider, OracleAbi, [
        [oracleAddress, "resultFor", [questionId]],
        [oracleAddress, "getFinalizeTS", [questionId]]
      ])
      return {
        executionApproved: BigNumber.from(result[0][0]).eq(BigNumber.from(1)), finalizedAt: BigNumber.from(result[1][0]).toNumber()
      }
    } catch (e) {
      console.error(e)
    }
  }
  return {
    executionApproved: false, finalizedAt: undefined
  }

}

export default class Plugin {
  public author = 'richard';
  public version = '0.0.1';
  public name = 'Dao Module';
  public website = 'https://safe.gnosis.io';
  public icon = 'https://raw.githubusercontent.com/gnosis/safe-docs/develop/website/static/img/gnosis_safe_logo_green.png';
  public options: any;

  validateTransaction(transaction: ModuleTransaction) {
    return (
      isBigNumberish(transaction.value) &&
      isAddress(transaction.to) &&
      (!transaction.data || isHexString(transaction.data)) &&
      transaction.operation in ['0', '1'] &&
      isBigNumberish(transaction.nonce)
    );
  }

  async calcTransactionHash(network: string, moduleAddress: string, transaction: ModuleTransaction) {
    const provider: JsonRpcProvider = getProvider(network)
    const domain = {
      "chainId": provider.network.chainId,
      "verifyingContract": moduleAddress,
    }
    return _TypedDataEncoder.hash(domain, EIP712_TYPES, transaction)
  }

  async calcTransactionHashes(chainId: number, moduleAddress: string, transactions: ModuleTransaction[]) {
    const domain = {
      "chainId": chainId,
      "verifyingContract": moduleAddress,
    }
    return transactions.map((tx) => {
      const txHash = _TypedDataEncoder.hash(domain, EIP712_TYPES, { nonce: "0", data: "0x", ...tx })
      console.log("[Plugin] Tx hash", txHash)
      return txHash
    })
  }

  async getExecutionDetails(network: string, moduleAddress: string, proposalId: string, transactions: ModuleTransaction[]): Promise<ProposalDetails> {
    console.log("[Plugin] Proposal", proposalId)
    console.log("[Plugin] Txs", transactions)
    const provider: JsonRpcProvider = getProvider(network)
    const contract = new Contract(moduleAddress, ModuleAbi, provider);

    const txHashes = await this.calcTransactionHashes(provider.network.chainId, moduleAddress, transactions)
    const nextTxIndex = await getNextTxIndex(provider, moduleAddress, proposalId, txHashes)
    const proposalEvents = await contract.queryFilter(contract.filters.ProposalQuestionCreated(null, proposalId))
    const questionId = proposalEvents.length > 0 ? proposalEvents[0].args?.questionId : undefined
    const moduleDetails = await getModuleDetails(provider, moduleAddress)
    const questionState = await checkPossibleExecution(provider, moduleDetails.oracle, questionId)
    try {
      return {
        ...moduleDetails,
        proposalId,
        questionId,
        ...questionState,
        nextTxIndex,
        transactions,
        txHashes
      };
    } catch (e) {
      throw new Error(e);
    }
  }

  async submitProposal(web3: any, moduleAddress: string, proposalId: string, transactions: ModuleTransaction[]) {
    console.log("[Plugin] submitProposal")
    const txHashes = await this.calcTransactionHashes(web3.network.chainId, moduleAddress, transactions)
    const tx = await sendTransaction(
      web3, moduleAddress, ModuleAbi, "addProposal", [proposalId, txHashes]
    )
    const receipt = await tx.wait();
    console.log(receipt)
  }

  async executeProposal(web3: any, moduleAddress: string, questionId: string, proposalId: string, transactions: ModuleTransaction[], transactionIndex: number) {
    console.log("[Plugin] executeProposal")
    const txHashes = await this.calcTransactionHashes(web3.network.chainId, moduleAddress, transactions)
    const moduleTx = transactions[transactionIndex]
    const tx = await sendTransaction(
      web3, moduleAddress, ModuleAbi, "executeProposal", [questionId, proposalId, txHashes, moduleTx.to, moduleTx.value, moduleTx.data || "0x", moduleTx.operation, moduleTx.nonce || "0"]
    )
    const receipt = await tx.wait();
    console.log(receipt)
  }
}
